.control {
	margin: 1rem 0 0;
	display: flex;
	align-items: stretch;
	flex-direction: column;
}

.control label,
.control input {
	display: block;
}

.control label {
	font-weight: bold;
	flex: 1;
	color: #464646;
	margin-bottom: 0.5rem;
}

.control input {
	flex: 3;
	font: inherit;
	padding: 0.35rem 0.35rem;
	border: 1px solid #ccc;
	box-shadow: 0 1px 3px 0 rgb(0 0 0 / 0.1), 0 1px 2px -1px rgb(0 0 0 / 0.1);
}

.control input:focus {
	outline: none;
	border-color: #4f005f;
	background: #f6dbfc;
}

.control.invalid input {
	border-color: red;
	background: #fbdada;
}

@media (min-width: 768px) {
	.control {
		align-items: center;
		flex-direction: row;
	}
}